ons || [];\n        if (interactor.options.find((option) => option.selected)) {\n            return;\n        }\n\n        interactor.options.unshift({ id: \"pleaseSelect\", text: \"pleaseSelect\" });\n    });\nconst requireds = {\n    interactors: props.options.interactors?.filter((interactor) => interactor.required).length || 0,\n    interracted: 0,\n};\nconst showBlockShadow = ref(requireds.interactors > 0);\n\nfunction decideClose(interactor: INotificationInteractor) {\n    if (!interactor.required) {\n        return;\n    }\n    if (requireds.interactors > ++requireds.interracted) {\n        return;\n    }\n\n    showBlockShadow.value = false;\n    emit(\"close-toast\");\n}\nfunction selected(interactor: INotificationInteractor, event: Event) {\n    clickedOption(interactor, (event.target as HTMLSelectElement).selectedIndex, event);\n}\nfunction clickedOption(interactor: INotificationInteractor, index: number, event: Event) {\n    const selectedOption = interactor.options![index];\n    const close = () => decideClose(interactor);\n    if (!interactor.customClose) {\n        close();\n    } else {\n        event.stopPropagation();\n    }\n    interactor.onInteract({ selectedOption: { text: selectedOption.text, id: selectedOption.id, index }, close });\n}\nfunction clicked(interactor: INotificationInteractor, event: MouseEvent) {\n    const close = () => decideClose(interactor);\n    if (!interactor.customClose) {\n        close();\n    } else {\n        event.stopPropagation();\n    }\n    interactor.onInteract({ close });\n}\n\nfunction getIconClass() {\n    if (props.options.type == \"error\") return \"mdi mdi-close-circle\";\n    else if (props.options.type == \"info\") return \"mdi mdi-information\";\n    else if (props.options.type == \"success\") return \"mdi mdi-check-circle\";\n    else if (props.options.type == \"warning\") return \"mdi mdi-alert\";\n}\n</script>\n\n<template>\n    <div class=\"notification-container\">\n        <template v-if=\"showBlockShadow\">\n            <teleport to=\"body\">\n                <div class=\"blockShadow\" :style=\"{ zIndex: props.zindex }\"></div>\n            </teleport>\n        </template>\n        <div class=\"notification-header\">\n            <i :class=\"iconClass + ' notification-icon' + ' col-1'\" :style=\"options.type == 'info' ? 'color: white' : 'color: black'\" />\n            <div :class=\"options.title ? 'notification-title col-2' : 'notification-text notification-text-padding col-2'\">\n                {{ options.title ? options.title : options.text }}\n            </div>\n            <div class=\"close-text mdi mdi-close\" :style=\"options.type == 'info' ? 'color: white' : 'color: black'\" />\n        </div>\n        <div>\n            <div v-if=\"options.title\" class=\"notification-text notification-text-margin\">{{ options.text }}</div>\n        </div>\n        <div class=\"button-container\">\n            <template v-for=\"(interactor, index) in options.interactors\" :key=\"index\">\n                <template v-if=\"interactor.options\">\n                    <select v-if=\"interactor.optionsType == 'combo'\" @click.stop=\"\" @change=\"selected(interactor, $event)\">\n                        <option v-for=\"option in interactor.options\" :selected=\"option.selected\" :key=\"option.id\">{{ option.text }}</option>\n                    </select>\n                    <template v-else>\n                        <button\n                            :class=\"'notification-confirmation-button' + (options.type == 'info' ? ' button-info' : '')\"\n                            v-for=\"(option, index) in interactor.options\"\n                            :key=\"option.id\"\n                            @click=\"clickedOption(interactor, index, $event)\"\n                        >\n                            {{ option.text }}\n                        </button>\n                    </template>\n                </template>\n            </template>\n        </div>\n    </div>\n</template>\n<style scoped>\n@import url(\"../../../styles/IDE3.css\");\n.blockShadow {\n    background-color: black;\n    top: 0px;\n    left: 0px;\n    position: fixed;\n    height: 100%;\n    width: 100%;\n    opacity: 0.7;\n}\n.center {\n    text-align: center;\n}\nh1 {\n    margin: 0px;\n}\n.action:hover {\n    border-color: black;\n}\n.notification-confirmation-button {\n    width: 90px;\n    height: 32px;\n    border-radius: 4px;\n    opacity: 1;\n    background-color: transparent;\n    margin-left: 8px;\n\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium) var(--unnamed-font-size-14) / var(--unnamed-line-spacing-19)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    font: normal normal medium 14px/19px Roboto;\n    letter-spacing: 0px;\n    opacity: 1;\n}\n.button-info {\n    border: 1px solid var(--white);\n    border: 1px solid #ffffff;\n    border-radius: 4px;\n    opacity: 1;\n    color: white;\n}\n\n.action {\n    height: 21px;\n    padding: 0px 6px;\n    border-radius: 5px;\n    cursor: pointer;\n    border: thin solid currentColor;\n    background-color: transparent;\n}\n.notification-icon {\n    font-size: 36px;\n    margin-top: 16px;\n    margin-left: 16px;\n    margin-bottom: 16px;\n}\n.notification-icon::before {\n    line-height: normal;\n}\n.notification-text {\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) / var(--unnamed-line-spacing-21)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    text-align: left;\n    font: normal normal normal 16px/21px Roboto;\n    opacity: 1;\n\n    width: 290px;\n    word-wrap: break-word;\n\n    margin-bottom: 24px;\n    word-wrap: break-word;\n}\n\n.notification-text-margin {\n    margin-left: 72px;\n    margin-top: -20px;\n}\n\n.notification-text-padding {\n    padding-top: 24px;\n    padding-left: 24px;\n}\n.close-text {\n    cursor: pointer;\n    margin-left: 12px;\n    margin-right: 16px;\n\n    height: 24px;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-24) / 29px Material Design Icons Desktop;\n    color: var(--white);\n    font: normal normal normal 24px/29px Material Design Icons Desktop;\n    letter-spacing: 0.43px;\n    color: #ffffff;\n    opacity: 1;\n    text-align: right;\n}\n.notification-container {\n    align-items: center;\n}\n\n.notification-header {\n    display: flex;\n    align-items: center;\n}\n.notification-title {\n    font: var(--unnamed-font-style-normal) normal bold var(--unnamed-font-size-16) / var(--unnamed-line-spacing-21) var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--white);\n    text-align: left;\n    font: normal normal bold 16px/21px Roboto;\n    letter-spacing: 0px;\n    opacity: 1;\n    padding-left: 24px;\n    width: 290px;\n    word-break: break-all;\n}\n\n.button-container {\n    float: right;\n    margin-right: 52px;\n    margin-bottom: 16px;\n}\n</style>\n\n<style>\n.Vue-Toastification__toast {\n    width: 388px;\n    box-shadow: 2px 2px 15px #00000066;\n    border-radius: 4px;\n    opacity: 0.9;\n    color: black;\n    padding: 0px;\n}\n.Vue-Toastification__toast--warning {\n    background: #f3ca40 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--success {\n    background: #29c660 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--error {\n    background: #ff6b6b 0% 0% no-repeat padding-box;\n}\n.Vue-Toastification__toast--info {\n    color: white;\n    background: #151233 0% 0% no-repeat padding-box;\n}\n</style>\n"],"sourceRoot":""}]);
